                <td>{{ props.item.name }}</td>
                <td>{{ props.item.gps }}</td>
                <td>
                    <v-btn icon
                        @click="removePath(props.item)"
                    >
                        <v-icon color="error">
                            fas fa-trash
                        </v-icon>
                    </v-btn>
                </td>
            </template>
        </v-data-table>
    </v-card>
</template>

<script>
import { mapMutations, mapState } from 'vuex';

export default {
  name: 'PaddockList',
  computed: {
    ...mapState([
      'paddocks',
    ]),
  },
  data: () => ({
    headers: [
      { text: 'Paddock Name', align: 'left', value: 'name' },
      {
        text: 'Coordinates', align: 'left', value: 'coordinates', sortable: false,
      },
      {
        text: '', align: 'left', value: 'actions', sortable: false,
      },
    ],
  }),
  methods: {
    ...mapMutations({
      removePath: 'REMOVE_PATH',
    }),
  },

