<template>
  <div>
    <v-card class="mt-0 mb-2 pa-3"
        color="primary"
        style="border-radius: 15px;"
        hover
    >
        <v-card-title
            style="font-size: 20pt;"
            class="primary white--text pa-2"
            

        >
            <v-icon color="secondary"
                class="mr-2"
            >
                fas fa-ruler-horizontal
            </v-icon>
            Paddocks
        </v-card-title>
        <PaddockList />
        </v-card>

        <v-card>
        <v-card-title
            style="font-size: 16pt;"
            class="mt-3"
        >
            <v-icon color="secondary"
                class="mr-2"
            >
                fas fa-draw-polygon
            </v-icon>
            Create New Paddocks
        </v-card-title>
        <v-text-field
            v-model="newPaddock"
            label="New Paddock Name"
            clearable
            outline
            prepend-icon="fas fa-map-marker-plus"
        >
        </v-text-field>
        <GmapMap
            ref="map"
            :center="user.farmCenter"
            :zoom="11"
            map-type-id="satellite"
            style="width: 100%; height: 69vh"
        >

            <div v-for="(paddock, index) in paddocks"
                :key="index"
            >
                <gmap-polygon
                    :paths="paddock.gps" :editable="true"
                    :clickable="true"
                    >
                </gmap-polygon>

                <gmap-custom-marker
                    :marker="paddock.gps[0]"
                    alignment="top"

                >
                    <v-card class="paddockName mb-3 ml-0 pa-0"
                        width="100%" height="30%"

                    >
                        <v-card-title class="pa-3"
                            style="font-weight: bold;"
                        >
                            {{ paddock.name }}
                        </v-card-title>
                    </v-card>

                </gmap-custom-marker>
            </div>
        </GmapMap>
    </v-card>
  </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';
import { loaded } from 'vue2-google-maps';
import GmapCustomMarker from 'vue2-gmap-custom-marker';
import PaddockList from './PaddockList.vue';

export default {
  name: 'PaddockBounds',
  computed: {
    ...mapState([
      'user',
      'paddocks',
    ]),
  },
  data: () => ({
    newPaddock: '',
  }),
  components: {
    'gmap-custom-marker': GmapCustomMarker,
    PaddockList,
  },
  methods: {
    ...mapMutations({
      removePath: 'REMOVE_PATH',
      editPath: 'EDIT_PATH',
    }),
    addPath(path) {
      this.editPath();
    },
    displayDrawTools() {
      const vm = this;
      const drawingManager = new google.maps.drawing.DrawingManager({
        drawingMode: google.maps.drawing.OverlayType.MARKER,
        drawingControl: true,
        drawingControlOptions: {
          position: google.maps.ControlPosition.TOP_CENTER,
          drawingModes: ['polygon', 'marker'],
        },
        markerOptions: { icon: 'https://developers.google.com/maps/documentation/javascript/examples/full/images/beachflag.png' },
      });
      drawingManager.setMap(this.$refs.map.$mapObject);
      google.maps.event.addListener(drawingManager, 'overlaycomplete', (event) => {
        const boundaries = event.overlay.getPaths();
        if (vm.newPaddock !== '') {
          vm.editPath({ name: vm.newPaddock, gps: boundaries.j[0].j });
          vm.newPaddock = '';
        } else {
          alert('New paddocks must have names.');
          event.overlay.setMap(null);
        }
      });
    },
  },
  mounted() {
    this.$gmapApiPromiseLazy().then(() => {
      const bounds = new google.maps.LatLngBounds();
      this.displayDrawTools();
    });
  },
};
</script>

